import React, { useEffect } from "react";
import Header from "../commonComponent/header";
import SideBar from "../commonComponent/sideBar";
import { Link, useNavigate, useParams } from "react-router-dom";
import { useForm } from "react-hook-form";
import { useAlert } from "react-alert";
import {
  editContent,
  getContentById,
} from "../../apiServices/home/homeHttpService";
import { showAlert } from "../commonComponent/alertManager";
import { useQuery } from "@tanstack/react-query";

function EditContent() {
  const {
    register,
    handleSubmit,
    reset,
    formState: { errors },
  } = useForm({
    mode: "onChange",
  });

  const alert = useAlert();
  const navigate = useNavigate();
  let { id } = useParams();

  const { data: details, isSuccess } = useQuery({
    queryKey: ["contentDetail", id],
    queryFn: async () => {
      return getContentById(id);
    },
    onError: (error) => {
      console.log(error);
    },
    select: (data) => data.results.content,
  });

  useEffect(() => {
    if (isSuccess && details) {
      const defaultValue = {
        titleEn: details?.titleEn || "",
        contentEn: details?.contentEn || "",
      };

      reset(defaultValue);
    }
  }, [isSuccess, details, reset]);

  const onSubmit = async (data) => {
    const response = await editContent(data, id);
    if (!response.error) {
      navigate("/admin/content-management");
      showAlert(alert, response.message, { timeout: 3000 });
      reset();
    } else {
      showAlert(alert, response.message, { timeout: 3000 });
    }
  };
  return (
    <>
      <Header />

      <div className="admin_main">
        <SideBar />
        <div className="admin_contentpart">
          <Link to="/admin/content-management" className="comman_btn w-fit">
            <i className="fa fa-arrow-left" />
          </Link>

          <div className="row comman_design mx-0 mt-4">
            <div className="col-12">
              <div className="row comman_table pt-md-4 pb-md-4 pb-3 px-md-4 px-2">
                <h3 className="mb-3 fs-5 fw-semibold">
                  Edit Content Management
                </h3>
                <form className="form-design" onSubmit={handleSubmit(onSubmit)}>
                  <div className="row">
                    <div className="col-md-6">
                      <div className="form-group">
                        <label htmlFor="form-label">Content Title</label>
                        <input
                          type="text"
                          className="form-control"
                          name="titleEn"
                          id="titleEn"
                          {...register("titleEn", {
                            required: true,
                          })}
                        />
                        {errors.titleEn &&
                          errors.titleEn.type === "required" && (
                            <p className="form-error">This field is required</p>
                          )}
                      </div>
                    </div>
                    <div className="col-md-6">
                      <div className="form-group">
                        <label htmlFor className="form-label">
                          Content Description
                        </label>
                        <textarea
                          row={4}
                          className="form-control"
                          name="contentEn"
                          id="contentEn"
                          {...register("contentEn", {
                            required: true,
                          })}
                        />
                        {errors.contentEn &&
                          errors.contentEn.type === "required" && (
                            <p className="form-error">This field is required</p>
                          )}
                      </div>
                    </div>
                    <div className="col-12">
                      <button type="submit" className="comman_btn w-fit">
                        Edit Content
                      </button>
                    </div>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default EditContent;
